import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login.vue'
import Home from './components/Home.vue'
import NotFound from './components/NotFound.vue'
import index from './components/page/index.vue'
import significance from './components/page/significance.vue'
import definition from './components/page/definition.vue'
import analysis from './components/page/analysis.vue'
import wearPrevention from './components/page/wearPrevention.vue'
import anticorrosionIndicators from './components/page/anticorrosionIndicators.vue'
import structureDisplay from './components/page/structureDisplay.vue'
import productionDemand from './components/page/productionDemand.vue'
import processingAnalysis from './components/page/processingAnalysis.vue'
import simulation from './components/page/simulation.vue'

import manufacturingCharacteristics from './components/page/manufacturingCharacteristics.vue'

import zhengce from './components/page/zhengce.vue'
import keXieXiangMu from './components/page/keXieXiangMu.vue'
import xueShuJiaoLiu from './components/page/xueShuJiaoLiu.vue'
import banShiZhiNan from './components/page/banShiZhiNan.vue'
import kePuFengCai from './components/page/kePuFengCai.vue'
import xueXiaoShouYe from './components/page/xueXiaoShouYe.vue'
import News from './components/page/News.vue'
import Administrator from './components/Administrator/AdminHome.vue'
import Welcome from './components/Administrator/Welcome.vue'
import Main from './components/Administrator/Main.vue'
import Edit from './components/Administrator/Edit.vue'
import EditLunBo from './components/Administrator/EditLunBo.vue'
import AdMain from './components/Administrator/AdMain.vue'
import AdEdit from './components/Administrator/AdEdit.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    { path: '/', redirect: '/home' },
    { path: '/login', component: Login },
    {
      path: '/administrator',
      component: Administrator,
      children: [
        { path: 'welcome', component: Welcome },
        { path: 'main', component: Main },
        { path: 'edit', component: Edit },
        { path: 'news', component: News },
        { path: 'editlunbo', component: EditLunBo },
        { path: 'admain', component: AdMain },
        { path: 'adedit', component: AdEdit }
      ]
    },
    {
      path: '/home',
      component: Home,
      children: [
        {
          path: '/home',
          redirect: 'index'
        },
        {
          path: 'index',
          component: definition
        },
        {
          path: 'significance',
          name: 'significance',
          component: significance
        },{
          path: 'definition',
          name: 'definition',
          component: definition
        },{
          path: 'wearPrevention',
          name: 'wearPrevention',
          component: wearPrevention
        },{
          path: 'anticorrosionIndicators',
          name: 'anticorrosionIndicators',
          component: anticorrosionIndicators
        },{
          path: 'productionDemand',
          name: 'productionDemand',
          component: productionDemand
        },{
          path: 'processingAnalysis',
          name: 'processingAnalysis',
          component: processingAnalysis
        },{
          path: 'simulation',
          name: 'simulation',
          component: simulation
        },{
          path: 'manufacturingCharacteristics',
          name: 'manufacturingCharacteristics',
          component: manufacturingCharacteristics
        },{
          path: 'structureDisplay',
          name: 'structureDisplay',
          component: structureDisplay
        }
        ,{
          path: 'analysis',
          name: 'analysis',
          component: analysis
        },
        
        {
          path: 'zhengce',
          name: 'zhengce',
          component: zhengce
        },
        {
          path: 'keXieXiangMu',
          name: 'keXieXiangMu',
          component: keXieXiangMu
        },
        {
          path: 'xueShuJiaoLiu',
          name: 'xueShuJiaoLiu',
          component: xueShuJiaoLiu
        },
        {
          path: 'banShiZhiNan',
          name: 'banShiZhiNan',
          component: banShiZhiNan
        },
        {
          path: 'kePuFengCai',
          name: 'kePuFengCai',
          component: kePuFengCai
        },
        {
          path: 'xueXiaoShouYe',
          name: 'xueXiaoShouYe',
          component: xueXiaoShouYe
        }
      ]
    },
    { path: '*', component: NotFound }
  ],
  scrollBehavior(to, from, savedPosition) {
    //页面跳转后自动回到顶部
    if (savedPosition) {
      return savedPosition
    }
    return { x: 0, y: 0 }
  }
})

// 挂载路由导航守卫
router.beforeEach((to, from, next) => {
  const tokenStr = window.sessionStorage.getItem('token')
  if (to.path === '/administrator/main' && !tokenStr) return next('/login')
  if (to.path === '/administrator/admain' && !tokenStr) return next('/login')
  if (to.path === '/administrator' && !tokenStr) return next('/login')
  if (to.path === '/administrator/edit' && !tokenStr) return next('/login')
  if (to.path === '/administrator/welcome' && !tokenStr) return next('/login')
  // if (!tokenStr) return next('/login')
  next()
})

export default router
