var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('el-row',{staticClass:"mart10"},[_c('el-card',{staticClass:"box-card"},[_c('el-col',[_c('div',{staticClass:"ql-editor"},[_c('p',[_vm._v("\n            叶轮机械是一种以连续旋转叶片为本体，使能量在流体工质与轴动力之间相互转换的动力机械。它不同于往复活塞式机械将工质密闭在变容积的空间中，而是与环境贯通，从而较前者具有更强大的通流能力，为大幅度提高机械的功率提供了有效途径。广义地讲，叶轮机械包括燃气轮机遵循流体力学中的连续性方程、动量方程、动量矩方程、能量方程。\n            设计时往往利用速度三角形和欧拉公式进行初步设计，我国科学家吴仲华教授在NACA工作时提出的三元流动理论（S1S2流面理论）是叶轮机械领域一座里程碑，对叶轮机械发展起到了极大帮助，至今仍在被广泛使用。现如今CFD技术的进步让叶轮机械的设计变得更加容易，但是由于CFD耗时较多，所以过去的设计方法仍在沿用。\n            叶轮机械是一个综合性的工程学科，涉及有流体力学、工程f热力学、计算流体力学、气体动力学、传热学、结构力学、材料学、控制科学等领域，还需要先进的加工、密封、轴承、测量等技术作为支撑，叶轮机械的先进程度考验了一个国家综合的科学技术实力。")]),_c('div',{staticClass:"img"},[_c('img',{attrs:{"src":require("../../assets/images/ylfx.png")}})])])])],1)],1)],1)}
