var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_c('el-row',{staticClass:"mart10"},[_c('el-card',{staticClass:"box-card"},[_c('el-col',[_c('div',{staticClass:"ql-editor"},[_vm._v("\n            造成离心风机叶轮磨损的因素有很多，而且多数是由于多重机理综合作用的结果，这会影响离心风机的整体使用效果，因而，进行叶轮磨损的措施非常必要。下面上海德惠风机为大家做个简要介绍。\n \n　　针对不同的磨损形式，可以将防磨损措施分为以下几种为：\n \n　　1、对叶片表面进行处理\n \n　　对叶片表面可以进行渗碳、等离子堆焊、喷涂硬质合金、粘贴陶瓷片处理。这些方法的共同优点是增加了叶片表面的硬度，从而在一定程度上 提高了叶片的耐磨性，但各种方法均存在各自的缺点。渗碳工艺难度大，实际渗碳时，渗碳层的部位和厚度要由叶片厚度和磨损情况以及渗碳 工艺决定;堆焊时叶片变形大，而且反复焊接会导致叶面产生裂缝，易产生事故;喷涂时涂层的厚度很难确定好;粘贴陶瓷片的效果比较好， 但价格高。\n \n　　2、表面喷涂耐磨涂层\n \n　　这种方法操作简单，成本低，但涂层磨损快，一次大约使用3—5个月。\n \n　　3、改进叶片结构\n \n　　共有将叶片工作面加工成锯齿状、变中空叶片为实心叶片、叶片加焊防磨块等方法，这些都可以在一定程度上降低叶轮的磨损。\n \n　　4、前置防磨叶栅\n \n　　在易磨损处安装防磨叶栅后，可以阻止粒子向后盘及叶根处流动，从而将粒子的集中磨损转化为均匀磨损，提高了叶轮的耐磨性，延长了风 机的使用寿命。\n \n　　5、改善气动设计\n \n　　合理选用风机进风口形状，设计时应保证叶轮入口相对速度，尽量降低通风机的转数，选择适当的叶轮流道形状，使叶片进口到出口的弧 度的曲率半径由小渐大，这样能减少固体颗粒与叶片的撞击机会。\n          ")])])],1)],1)],1)}
