<template>
  <el-container class="home-container">
    <!-- 时间 -->
    <el-row class="welcome">
      <span>欢迎! &nbsp&nbsp今天是{{ timeInfo.year }}年{{ timeInfo.month + 1 }}月{{ timeInfo.day }}日 </span>
      <span style="margin-left: 18px">星期{{ week2 }} </span>
      <span style="margin-left: 18px">当前时间 {{ timeInfo.hours }}: {{ timeInfo.minutes }}: {{ timeInfo.secondes }} </span>
    </el-row>
    <!-- 主页头部 -->
    <el-header>
      <div>
        <div class="title">面向智能制造过程的叶片非接触式的数据识别与提取研究</div>
      </div>
    </el-header>
    <!-- 主页导航栏 -->
    <el-row>
      <el-menu mode="horizontal" background-color="rgb(64, 112, 186)" text-color="white" class="menu" active-text-color="#000000" :default-active="activeIndex" @select="handleSelect">
        <el-submenu :key="index"  v-for="(item, index) in menuList" :index="index" >
          <template slot="title"> {{ item.name }}</template>
          <el-menu-item  v-for="(i, idx) in item.menu" :index="i.id.toString()" :key="idx">
            {{ i.name }}
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </el-row>
   <el-container>
      <el-main class="main">
        <router-view class="box"></router-view>
      </el-main>
    </el-container>
   <el-footer>@赵庭昱</el-footer>
  </el-container>
</template>

<script>
import axios from 'axios'
export default {
  name: '',
  data() {
    return {
      timeInfo: {
        currentTime: '',
        year: '',
        month: '',
        day: '',
        week: '',
        hours: '',
        minutes: '',
        secondes: ''
      },
      activeIndex: '11',
      menuList: [
        { name: '基础属性',menu:[ { name: '名词解释', id: 11 },{ name: '叶轮分析', id: 12 },{ name: '磨损防止', id: 13 },{ name: '防腐指标', id: 14 },{ name: '结构展示', id: 15 }] },
        { name: '研究现状',menu:[ { name: '生产需求', id: 21 },{ name: '制造特点', id: 22 }] },
        { name: '加工过程',menu:[ { name: '加工分析', id: 31},{ name: '模拟仿真', id: 32}]},
        { name: '数据处理',menu:[ { name: 'Hadoop 搭建', id: 41 },{ name: '数据换算', id: 42 },{ name: '数据合并', id: 43 },{ name: '数据分组', id: 44 }
        ,{ name: '数据设计', id: 48 },{ name: '对xlsx文档操作', id: 49 },{ name: '按照某一数据排列', id: 50 }]},
        { name: '研究意义',menu:[ { name: '社会意义，学术意义', id: 51 }]}
      ],
      menuList2: []
    }
  },
  created() {},
  mounted() {
    this.initTime()
    setInterval(this.initTime, 1000)
    let api3 = 'http://localhost:7070/news_type/all'
    axios
      .get(api3)
      .then(res => {
        // console.log(res);
        this.menuList2 = res.data.data
      })
      .catch(error => {
        console.log(error)
      })
  },
  watch: {},
  computed: {
    week2() {
      if (this.timeInfo.week == 1) return '一'
      else if (this.timeInfo.week == 2) return '二'
      else if (this.timeInfo.week == 3) return '三'
      else if (this.timeInfo.week == 4) return '四'
      else if (this.timeInfo.week == 5) return '五'
      else if (this.timeInfo.week == 6) return '六'
      else return '日'
    }
  },
  methods: {
    formatterTitle(t) {
      switch (t) {
        case '11':
          return 'definition'
        case '12':
          return 'analysis'
        case '13':
          return 'wearPrevention'
        case '14':
          return 'anticorrosionIndicators'
        case '15':
          return 'structureDisplay'
        case '21':
          return 'productionDemand'
        case '22':
          return 'manufacturingCharacteristics'
        case '31':
          return 'processingAnalysis'
        case '32':
          return 'simulation'
        case '51':
          return 'significance'
      }
    },
    handleSelect(key, keyPath) {
      if (key == '28') {
        window.open('https://www.pdsu.edu.cn/#')
      } else {
        this.$router.push({
          path: '/home/' + this.formatterTitle(key),
          query: {
            title: this.formatterTitle(key),
            id: key
          }
        })
      }
    },
    // 初始化时间
    initTime() {
      this.timeInfo.currentTime = new Date()
      this.timeInfo.year = this.timeInfo.currentTime.getFullYear()
      this.timeInfo.month = this.timeInfo.currentTime.getMonth()
      this.timeInfo.day = this.timeInfo.currentTime.getDate()
      this.timeInfo.week = this.timeInfo.currentTime.getDay()
      this.timeInfo.hours = this.formattingTime(this.timeInfo.currentTime.getHours())
      this.timeInfo.minutes = this.formattingTime(this.timeInfo.currentTime.getMinutes())
      this.timeInfo.secondes = this.formattingTime(this.timeInfo.currentTime.getSeconds())
    },
    // 格式化时间——两位数
    formattingTime(val) {
      if (val < 10) {
        val = '0' + val
      }
      return val
    }
  }
}
</script>

